      position: relative;
      height: 100%;
      flex-grow: 1; }
    .audio_player .audio_progress {
      background-color: #3594a3;
      height: 100%;
      width: 0;
      will-change: width; 
    }
    .audio_player .audio_progress_overlay {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between; }
    .audio_player .audio_info_marquee {
      overflow: hidden;
