        display: initial;
        animation: marquee5 10s cubic-bezier(0, 0.23, 0.6, -0.09) infinite; } }
    @media screen and (min-width: 450px) {
      .audio_player .audio_info {
        animation: marquee4 10s cubic-bezier(0, 0.23, 0.7, -0.09) infinite; } }
