    @media screen and (min-width: 650px) {
      .audio_player .audio_info {
        animation: marquee2 10s cubic-bezier(0, 0.23, 1, -0.09) infinite; } }
    @media screen and (min-width: 720px) {
      .audio_player .audio_info {
        animation: initial; } }
